import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'


const MicrosoftProgram = () => (
  <div className="container p-3 p-md-5 main-content mt-80">
    <div className="AresHackathon-container" style={{position: 'relative', overflow: 'hidden'}}>
      <Container>
        <Row className="show-grid pt-40 pb-40 pr-25 pl-25">
          <Col  md={12}>
            <h1 className="display-3 headline" style={{ color: '#ff6427', fontWeight: 'bold' }}>
              <FormattedMessage id="OkGaEx.title" />
            </h1>
          </Col>
          <Col  md={4}>
            <img src="/images/exchange.jpg" width="100%" height="100%" alt="" />
          </Col>
          <Col  md={4} className="pr-0">
            <p style={{ color: '#ff6427', marginTop: '20px', fontSize: '1rem' }}>
            <FormattedMessage id="introduce.okgaex.title" />
            </p>
          </Col>
          <Col  md={4}>
            <div className="button-maintain-suite">
                <a
                    style={{ textDecoration: 'none' }}
                    href="https://medium.com/ares-tech/weekly-report-for-ares-september-10-16-345e9458c10e"
                    rel="noopener noreferrer"
                    target="_blank"
                >
              <Button className="button-4">
              <FormattedMessage id="checkout.title" />
              </Button>
                </a>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  </div>
)

export default MicrosoftProgram



