import React, { Component } from 'react'
import { Container, Row, Col } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './Footer.css'



class FooterConnectPage extends Component {
  render() {
    const { config, langKey } = this.props
    const { footerh1, address } = config

    return (
      <footer className="footerConnect">
        <Container>
          <Row>
          <Col  md={2} sm={1}></Col>
          <Col  md={4} sm={5}>
          <h3>
            <FormattedMessage id="get.regular" /><br/>
            <FormattedMessage id="updates.on.ares" />
            </h3>
          <p className="text-white">
            <FormattedMessage id="latest.news" /><br/>
            <FormattedMessage id="your.inbox" />
            </p>
          </Col>
          <Col  md={4} sm={5}>
            <form>
              <div className="form-group col-xs-12">
                    <label className="connectcontrol-label">
                      <FormattedMessage id="enter.your.email.below" />
                    </label>
                    <input type="email" className="form-control" placeholder="Email Address" required=""/>
                </div>
                <div className="col-xs-12">
                    <button type="submit" className="btn btn9">
                    <FormattedMessage id="send.me.updates" />
                    </button>
                </div>
            </form>
          </Col>
          <Col  md={2} sm={1}></Col>
          </Row>
        </Container>
      </footer>
    )
  }
}

export default FooterConnectPage



