import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'


const AresHackathon = () => (
  <div className="container p-3 p-md-5 pt-0 pb-0 main-content">
    <div className="AresHackathon-container" style={{position: 'relative', overflow: 'hidden'}}>
      <Container>
        <Row className="show-grid pt-40 pb-40">
            <Col  md={12}>
                <h1 className="display-3 headline" style={{ color: '#ff6427', fontWeight: 'bold' }}>
                <FormattedMessage id="AresHackathon.title" />
                </h1>
            </Col>
            <Col  md={4} >
            <img src="/images/dorahack01.jpeg" width="100%" height="100%" alt="" />
            </Col>
            <Col  md={4} className="pr-0">
            <p style={{ color: '#ff6427', marginTop: '20px', fontSize: '1rem' }}>
              <FormattedMessage id="introduce.dorahack.title" />
              </p>
            </Col>
            <Col  md={4}>
            <div className="button-maintain-suite">
                <a
                    style={{ textDecoration: 'none' }}
                    href="https://medium.com/ares-tech/weekly-report-for-ares-october-29-november-4-d49640022dea"
                    rel="noopener noreferrer"
                    target="_blank"
                >
                <Button className="button-4">
                <FormattedMessage id="checkout.title" />                                                               
                </Button>
                </a>
            </div>
            </Col>
        </Row>
    </Container>
    </div>
  </div>
)

export default AresHackathon



