import React from 'react'
import { Container, Row, Col, Card, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import { chunk } from 'lodash'
import './index.css'
import partners from './partners'

const FormatIntlLink = ({ id, link }) => (
  <FormattedMessage id={id}>
    {name => (
      <a href={link} rel="noopener noreferrer" target="_blank" style={{ color: '#ce38da' }}>
        {name}
      </a>
    )}
  </FormattedMessage>
)

const Partners = () => (
  <div className="container p-3 p-md-5 pt-0 pb-0 main-content">
    <div className="PartnersAresConnect-container" style={{position: 'relative', overflow: 'hidden'}}>
      <Container>
        <Row className="show-grid pt-40 pb-40">
            <Col  md={12}>
                <h1 className="display-3 headline" style={{ color: '#ff6427', fontWeight: 'bold' }}>
                <FormattedMessage id="PartnersAresConnect.title" />
                </h1>
            </Col>
            <Container>
       {chunk(partners, 3).map((row, index) => (
        <Row key={index}>
          {row.map(partner => {
            if (partner.link.trim())
              return <Col md={4} key={partner.name} className="pb-4">
                <Card style={{border: 'none', backgroundColor: 'transparent'}}>
                  <a className="partners-link" href={partner.link} alt={partner.name} target="_blank">
                    <div
                      style={{
                        backgroundImage: `url(${partner.imageSrc})`,
                        backgroundPosition: 'center',
                        height: '100%',
                        backgroundSize: '70%',
                        backgroundRepeat: 'no-repeat',
                      }}
                    />
                  </a>
                </Card>
              </Col>
            else
              return <Col md={4} key={partner.name} className="pb-4">
                <Card style={{border: 'none', backgroundColor: 'transparent'}}>
                  <a className="partners-link" alt={partner.name} target="_blank">
                    <div
                      style={{
                        backgroundImage: `url(${partner.imageSrc})`,
                        backgroundPosition: 'center',
                        height: '100%',
                        backgroundSize: '70%',
                        backgroundRepeat: 'no-repeat',
                      }}
                    />
                  </a>
                </Card>
              </Col>
          })}
        </Row>
      ))}
    </Container>
        </Row>
    </Container>
    </div>
  </div>
)

export default Partners



