import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import '../style/custom.css'
import IntlProvider from '../util/intlProvider'
import config from '../../data/SiteConfig'
import MainLayout from '../util/mainLayout'
import NavBar from '../components/Navbar'
import ConnectMicrosoftProgram from '../components/ConnectMicrosoftProgram'
import AresHackathon from '../components/AresHackathon'
import AresOkGaEx from '../components/AresOkGaEx'

import PartnersAresConnect from '../components/PartnersAresConnect'
import FooterConnectPage from '../components/FooterConnectPage/Footer'
 
 
const Section = ({ children, className }) => <div className={className}>{children}</div>
 
const Main = ({ langKey, messages }) => (
  <IntlProvider langKey={langKey} messages={messages}>
    <div>
      <NavBar langKey={langKey} color="Sara" />
      <MainLayout langKey={langKey} theme="mainconnect" footerTheme="footer">
        <Section>
          <ConnectMicrosoftProgram />
        </Section>
        <Section>
          <AresHackathon />
        </Section>
          <Section>
              <AresOkGaEx />
          </Section>

          <Section>
          <PartnersAresConnect />
        </Section>
        <FooterConnectPage config={config} langKey={langKey} />
      </MainLayout>
    </div>
  </IntlProvider>
)
 
export default Main



