import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'


const MicrosoftProgram = () => (
  <div className="container p-3 p-md-5 main-content mt-80">
    <div className="ConnectMicrosoftProgram-container" style={{position: 'relative', overflow: 'hidden'}}>
      <Container>
        <Row className="show-grid pt-40 pb-40 pr-25 pl-25">
          <Col  md={12}>
            <h1 className="display-3 headline" style={{ color: '#fff', fontWeight: 'bold' }}>
              <FormattedMessage id="MicrosoftProgram.title" />
            </h1>
          </Col>
          <Col  md={4}>
            <img src="/images/microsoft.jpg" width="100%" height="100%" alt="" />
          </Col>
          <Col  md={4} className="pr-0">
            <p style={{ color: '#ffffff', marginTop: '20px', fontSize: '1rem' }}>
            <FormattedMessage id="introduce.microsoft.title" />
            </p>
          </Col>
          <Col  md={4}>
            <div className="button-maintain-suite">
                <a
                    style={{ textDecoration: 'none' }}
                    href="https://www.chainnews.com/news/201600649279.htm"
                    rel="noopener noreferrer"
                    target="_blank"
                >
              <Button className="button-12">
              <FormattedMessage id="apply.title" />
              </Button>
                </a>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  </div>
)

export default MicrosoftProgram



